import React from 'react';
import styles from './ControlPanel.module.scss';
import keyboard from '../../images/keyboard-logo.svg';

const ControlPanel = ({ withOutBackButton, withOutKeyboardButton, openPopup, onClose }) => {
  return (
    <>
      <section className={styles.ontrolPanel}>
        {withOutBackButton && (
          <button className={styles['ontrolPanel__back-button']} onClick={onClose}>
            
          </button>
        )}
        {!withOutKeyboardButton && (
          <button onClick={openPopup} className={styles['ontrolPanel__keyboard-button']}>
            <img src={keyboard} alt="keyboard-logo" />
              
          </button>
        )}
      </section>
    </>
  );
};

export default ControlPanel;
