import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import LoadingScreen from '../../components/LoadingScreen/LoadingScreen';

import OrderApi from '../../api/generated/src/api/OrderApi';
import ApiClient from '../../api/generated/src/ApiClient';

class CustomApiClient extends ApiClient {
  constructor(params, baseUrl) {
    super(params);
    this.basePath = baseUrl;
  }
}

//    ApiClient   baseUrl

const SearchPage = () => {
  let navigate = useNavigate();

  //    API
  const apiClient = new CustomApiClient(undefined, 'https://api.example.com');

  //   API 
  const orderApi = new OrderApi(apiClient);

  useEffect(() => {
    const routeByTime = setTimeout(() => navigate('/main'), 115000);

    console.log(11);

    //    order_key
    orderApi.apiOrdersGenerateOrderKeyGet((error, data, response) => {
      if (error) {
        console.error('Error generating order key:', error);
      } else {
        const orderKey = data.order_key;
        console.log('Generated order key:', orderKey);

        //    
        orderApi.apiOrdersOrderKeyPackGet(orderKey, (error, data, response) => {
          if (error) {
            console.error('Error getting order information:', error);
          } else {
            console.log('Order information:', data);

            //     formed
            orderApi.apiOrdersOrderKeyFormedPatch(orderKey, (error, data, response) => {
              if (error) {
                console.error('Error changing order status to formed:', error);
              } else {
                console.log('Order status changed to formed:', data);

                //  ,    canceled
                orderApi.apiOrdersOrderKeyCancelPatch(orderKey, (error, data, response) => {
                  if (error) {
                    console.error('Error changing order status to canceled:', error);
                  } else {
                    console.log('Order status changed to canceled:', data);
                  }
                });
              }
            });
          }
        });
      }
    });

    console.log(22);

    return () => clearTimeout(routeByTime);
  }, []);

  return <LoadingScreen />;
};

export default SearchPage;
